    span.alt,
    summary {
      letter-spacing: -0.01em;
    }

    p strong,
    td.content strong,
    div.footnote strong {
      letter-spacing: -0.005em;
    }

    p,
    blockquote,
    dt,
    td.content,
    td.hdlist1,
    span.alt,
    summary {
      font-size: 1.0625rem;
    }

    p {
      margin-bottom: 1.25rem;
    }

    .sidebarblock p,
    .sidebarblock dt,
    .sidebarblock td.content,
    p.tableblock {
      font-size: 1em;
    }

    .exampleblock > .content {
      background: #fffef7;
      border-color: #e0e0dc;
      box-shadow: 0 1px 4px #e0e0dc;
    }

    .print-only {
      display: none !important;
    }

    @page {
      margin: 1.25cm 0.75cm;
    }

    @media print {
      * {
        box-shadow: none !important;
        text-shadow: none !important;
      }

      html {
        font-size: 80%;
      }

      a {
        color: inherit !important;
        text-decoration: underline !important;
      }

      a.bare,
      a[href^="#"],
      a[href^="mailto:"] {
        text-decoration: none !important;
      }

      a[href^="http:"]:not(.bare)::after,
      a[href^="https:"]:not(.bare)::after {
        content: "(" attr(href) ")";
        display: inline-block;
        font-size: 0.875em;
        padding-left: 0.25em;
      }

      abbr[title] {
        border-bottom: 1px dotted;
      }

      abbr[title]::after {
        content: " (" attr(title) ")";
      }

      pre,
      blockquote,
      tr,
      img,
      object,
      svg {
        page-break-inside: avoid;
      }

      thead {
        display: table-header-group;
      }

      svg {
        max-width: 100%;
      }

      p,
      blockquote,
      dt,
      td.content {
        font-size: 1em;
        orphans: 3;
        widows: 3;
      }

      h2,
      h3,
      #toctitle,
      .sidebarblock > .content > .title {
        page-break-after: avoid;
      }

      #header,
      #content,
      #footnotes,
      #footer {
        max-width: none;
      }

      #toc,
      .sidebarblock,
      .exampleblock > .content {
        background: none !important;
      }

      #toc {
        border-bottom: 1px solid #dddddf !important;
        padding-bottom: 0 !important;
      }

      body.book #header {
        text-align: center;
      }

      body.book #header > h1:first-child {
        border: 0 !important;
        margin: 2.5em 0 1em;
      }

      body.book #header .details {
        border: 0 !important;
        display: block;
        padding: 0 !important;
      }

      body.book #header .details span:first-child {
        margin-left: 0 !important;
      }

      body.book #header .details br {
        display: block;
      }

      body.book #header .details br + span::before {
        content: none !important;
      }

      body.book #toc {
        border: 0 !important;
        text-align: left !important;
        padding: 0 !important;
        margin: 0 !important;
      }

      body.book #toc,
      body.book #preamble,
      body.book h1.sect0,
      body.book .sect1 > h2 {
        page-break-before: always;
      }

      .listingblock code[data-lang]::before {
        display: block;
      }

      #footer {
        padding: 0 0.9375em;
      }

      .hide-on-print {
        display: none !important;
      }

      .print-only {
        display: block !important;
      }

      .hide-for-print {
        display: none !important;
      }

      .show-for-print {
        display: inherit !important;
      }
    }

    @media amzn-kf8, print {
      #header > h1:first-child {
        margin-top: 1.25rem;
      }

      .sect1 {
        padding: 0 !important;
      }

      .sect1 + .sect1 {
        border: 0;
      }

      #footer {
        background: none;
      }

      #footer-text {
        color: rgba(0, 0, 0, 0.6);
        font-size: 0.9em;
      }
    }

    @media amzn-kf8 {
      #header,
      #content,
      #footnotes,
      #footer {
        padding: 0;
      }
    }
}